body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-height: 90vh;
}

.home header {
  text-align: center;
}
.about h2 {
  width: 100%;
  text-align: left;
  padding: 3rem 0 1.5rem 0;
}

.about main {
  display: flex;
}

.about main p {
  padding-bottom: 2rem;
}

.about aside {
  margin-left: 2.5rem;
}

.project ul {
  display: flex;
  width: 100%;
  margin-bottom: 1.5rem;
}

.project li {
  padding: 6px;
}

.project img {
  display: block;
  width: 100%;
}

.project h2,
.project h3 {
  font-size: 2.7rem;
  font-style: normal;
  font-weight: 700;
  text-align: left;
}

.project h2 {
  margin: 6rem 0 0 0;
}

.project h3 {
  font-style: italic;
  font-weight: 100;
}

.project .description {
  width: 50%;
  padding: 2rem 0;
}

.gotoapp {
  display: flex;
}

.gotoapp span {
  margin-right: 1rem;
}
.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-height: 90vh;
}

.portfolio header {
  text-align: center;
}

.items li {
  display: flex;
  margin-bottom: 6rem;
}

.items li h3 {
  font-style: italic;
  padding-bottom: 1rem;
}

.items li aside {
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
}

.items li img {
  width: 50%;
  padding-right: 2%;
}

.items li .arrow {
  width: 4rem;
  margin-top: 1rem;
}
.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
}

.main-header a {
  font-family: "mencken-std";
  font-size: 2.3vw;
  text-decoration: none;
  text-transform: uppercase;
  color: #0A0A0A;
}

.main-header a:hover{
  color: #00ff31;


}

.main-header nav li {
  display: inline-block;
  padding-left: 1.5vw;
}
